/* eslint-disable jsx-a11y/alt-text */
import React, { Component } from 'react'
import { Button, Container, Header, Icon, Menu, Responsive, Segment, Sidebar, Visibility } from 'semantic-ui-react'
import { Link } from 'react-router-dom'
import logo from '../../assets/logo.png'

/**
 * TODO:
 * - Add something moving in the background
 * - Add react spring animations
 * - Get all quotes fixed
 * - Migrate to functional components
 */

const getWidth = () => {
    const isSSR = typeof window === 'undefined'
    return isSSR ? Responsive.onlyTablet.minWidth : window.innerWidth
}

const Logo = ({ mobile }) => {
    return (
        <img style={{
            marginTop: mobile ? '0.5em' : '1.5em',
            width: '50%',
            maxWidth: '225px'
        }}
            className="landing-page-logo"
            src={logo}
        />
    )
}

/* eslint-disable react/no-multi-comp */
const HomepageHeading = ({ mobile }) => (
    <Container text>
        <Link to="/"><Logo mobile/></Link>
        <Header
            as='h1'
            content='UniPoint'
            inverted
            style={{
                fontSize: mobile ? '2em' : '4em',
                fontWeight: 'normal',
                marginBottom: 0
            }}
        />
        {/* IDEA: use React Spring here to change the quote. */}
        <Header
            as='h2'
            content="Don't spend more than £30,000 on the future you never wanted."
            inverted
            style={{
                fontSize: mobile ? '1.5em' : '1.7em',
                fontWeight: 'normal',
                marginTop: mobile ? '0.5em' : '1.5em',
            }}
        />
        <Link to="/studies">
            <Button primary size='huge'>
                Get Started
                <Icon name='right arrow' />
            </Button>
        </Link>
    </Container>
)

class DesktopContainer extends Component {
    state = {}

    hideFixedMenu = () => this.setState({ fixed: false })
    showFixedMenu = () => this.setState({ fixed: true })

    render() {
        const { children } = this.props
        const { fixed } = this.state

        return (
            <Responsive getWidth={getWidth} minWidth={Responsive.onlyTablet.minWidth}>
                <Visibility
                    once={false}
                    onBottomPassed={this.showFixedMenu}
                    onBottomPassedReverse={this.hideFixedMenu}
                >
                    <Segment
                        inverted
                        textAlign='center'
                        style={{ minHeight: '100vh', overflowY: "hidden", padding: '1em 0em' }}
                        vertical
                    >
                        <Menu
                            fixed={fixed ? 'top' : null}
                            inverted={!fixed}
                            pointing={!fixed}
                            secondary={!fixed}
                            size='large'
                            style={{flexDirection: 'row-reverse'}}
                        >
                            <Container>
                                <Menu.Item as={Link} to="/studies">Studies</Menu.Item>
                            </Container>
                        </Menu>
                        <HomepageHeading />
                    </Segment>
                </Visibility>

                {children}
            </Responsive>
        )
    }
}

class MobileContainer extends Component {
    state = {}

    handleSidebarHide = () => this.setState({ sidebarOpened: false })

    handleToggle = () => this.setState({ sidebarOpened: true })

    render() {
        const { children } = this.props
        const { sidebarOpened } = this.state

        return (
            <Responsive
                as={Sidebar.Pushable}
                getWidth={getWidth}
                maxWidth={Responsive.onlyMobile.maxWidth}
            >

                <Sidebar
                    as={Menu}
                    animation="overlay"
                    direction="right"
                    inverted
                    onHide={this.handleSidebarHide}
                    vertical
                    visible={sidebarOpened}
                    
                >
                    <Menu.Item as={Link} to="/studies">
                        <Icon name='book'/>
                        Studies
                    </Menu.Item>
                </Sidebar>
                <Sidebar.Pusher dimmed={sidebarOpened}>
                    <Segment
                        inverted
                        textAlign='center'
                        style={{ height: '100vh', padding: '1em 0em' }}
                        vertical
                    >
                        <Container>
                            <Menu inverted pointing secondary size='large' style={{flexDirection: 'row-reverse'}}>
                                <Menu.Item onClick={this.handleToggle}>
                                    <Icon name='sidebar' />
                                </Menu.Item>
                            </Menu>
                        </Container>
                        <HomepageHeading mobile />
                    </Segment>

                    {children}
                </Sidebar.Pusher>

            </Responsive>
        )
    }
}

const ResponsiveContainer = ({ children }) => (
    <div>
        <DesktopContainer>{children}</DesktopContainer>
        <MobileContainer>{children}</MobileContainer>
    </div>
)

const HomepageLayout = () => <ResponsiveContainer/>

export default HomepageLayout