import React, { useReducer } from 'react'
import { observer, inject } from 'mobx-react'
import { useState, useEffect } from 'react';
import '../../styles/Studies.css'
import StudyItem from '../items/StudyItem';
import { Item, Modal, Button, Divider, Input, Icon } from 'semantic-ui-react';
import { Skeleton } from 'antd';

import logo from '../../assets/logo.png'

/**
 * TODO:
 * - Create the controlled form
 * - Get submission working
 * - Mock some stuff in mobx
 */
const FeedbackModal = inject("store")(observer((props) => {

    const [ loading, setLoading ] = useState(false);
    const [ sent, setSent ] = useState(false);

    const reducer = (state, { field, value }) => {
        return {
            ...state,
            [field]: value
        }
    }
    
    const [ state, dispatch ] = useReducer(reducer, { email: '' })

    const onChange = (event) => {
        dispatch({ field: event.target.name, value: event.target.value })
    }

    const subscribeToNewsletter = () => {
        if (state.email === "") {
            return
        }
        
        setLoading(true);
        /**
         * TODO: Use actual async information in order to set loading
         */
        setTimeout(() => {
            props.store.subscribeToNewsletter(state.email); 
            setLoading(false);
            setSent(true);
        }, 800);
    }

    return(
        <Modal dimmer="blurring" trigger={props.trigger} closeIcon>
            <Modal.Header>Interested in something that is missing?</Modal.Header>
            <Modal.Content image>
            <Modal.Description>
                <div>
                    <h3>Let us know</h3>
                    <p>
                        We would be glad to hear you out and listen to your feedback!
                        Feel free to send an e-mail at: <b>contact@uni-point.co.uk</b>
                    </p>   
                </div>
                <Divider/>
                <div>
                    <h3>Subscribe to our newsletter!</h3>
                    <p>
                        If you are interested in the future development of this platform, keep in touch by subscribing to our newsletter.
                    </p>
                    <Input iconPosition='left' placeholder='Email'>
                        <Icon name='at' />
                        <input name="email" onChange={onChange} />
                    </Input>
                </div>
                {/* Form here */}
            </Modal.Description>
            </Modal.Content>
            <Modal.Actions>
                <Button disabled={sent} loading={loading} primary positive onClick={subscribeToNewsletter}>
                    {sent ? "Done" : "Subscribe"} <Icon name='chevron right' />
                </Button>
            </Modal.Actions>
        </Modal>
    )
}))

const Studies = inject("store")(observer((props) => {

    const [ loading, setLoading ] = useState(true);
    const [ items, setItems ] = useState([]);

    useEffect(() => {
        async function fetchItems() {
            props.store.fetchStudies().then((jsonArray) => {
                setItems(jsonArray);
                setLoading(false);
            })
        }

        fetchItems();
    }, []);

    if (loading) {
        return (
            <div className="root-studies-container">
                <div className="studies-header"></div>
                <div className="studies-container"><Skeleton active/><Skeleton active/></div>
            </div>
        )
    }

    return (
        <div className="root-studies-container">
            <div className="studies-header">
                    <h1>
                        Start your journey by exploring!
                    </h1>
                    <h3>
                        Here are a few suggestions based on your answers.
                    </h3>
            </div>
            <div className="studies-container">
                <Item.Group>
                    {items.map((el) => 
                        <StudyItem 
                            key={el.id} 
                            id={el.id} 
                            title={el.name} 
                            description={el.shortDescription} 
                            imageSrc={el.image}
                            rating={el.rating}/>)}
                    
                </Item.Group>
            </div>
            <FeedbackModal trigger={
                <div className="float-feedback">
                    <img className='float-feedback-logo' alt="UniPoint" src={logo}/>
                </div>
            }/>
        </div>
    )
}))

export default Studies
