import React from 'react'
import { Item, Icon } from 'semantic-ui-react'
import { Link } from 'react-router-dom'

function StudyItem(props) {
    return (
        <Item className="study-item" as={Link} to={"/study/" + props.id}>
            <Item.Image fluid className="study-image" src={props.imageSrc} />
            <Item.Content>
                <Item.Header>{props.title}</Item.Header>
                <Item.Description>{props.description}</Item.Description>
                <Item.Extra>
                <Icon color='yellow' name='star' /> Rated {props.rating}/10
                </Item.Extra>
            </Item.Content>
        </Item>
    )
}

export default StudyItem
