import React from 'react';
import {
	Switch,
	Route,
	useHistory,
	Redirect
} from "react-router-dom";
import { observer, inject } from 'mobx-react';

import LandingPage from './components/core/LandingPage'
import Questionnaire from './components/core/Questionnaire'
import Studies from './components/core/Studies'
import StudyPage from './components/core/StudyPage'
import About from './components/core/About'
import ReactGA from 'react-ga';

import { Result, Button } from 'antd';
import { Link } from 'react-router-dom'

const NotFoundPage = () => {
	return (<Result
    status="404"
    title="404"
    subTitle="Sorry, the page you visited does not exist."
    extra={<Button type="primary" as={Link} to="/"><Link to="/">Back Home</Link></Button>}
  />)
}

const App = inject("store")(observer((props) => {

	let history = useHistory();
	
	props.store.init()

	history.listen((location) => {
		ReactGA.set({ page: location.pathname });
		ReactGA.pageview(location.pathname)
	  }
	);

	let routes = {
		LandingPage: () => <LandingPage/>,
		Studies: () => props.store.alreadyAnswered ? <Studies/> : <Redirect to="/questions"/>,
		StudyPage: (data) => <StudyPage {...data.match.params}/>,
		Questions: () => <Questionnaire/>,
		About: () => <About/>
	}
	
	return (
		<Switch>
			<Route exact path={`/`} component={routes.LandingPage} />
			<Route exact path={`/studies`} component={routes.Studies} />
			<Route exact path={`/questions`} component={routes.Questions} />
			<Route exact path={`/study/:studyId`} component={routes.StudyPage} />
			<Route exact path={`/about`} component={routes.About} />
			<Route component={NotFoundPage}/>
		</Switch>
	);
}))

export default App;
