    wait(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    init() {
        this.alreadyAnswered = window.localStorage.getItem("alreadyAnswered") === "true"
        
        function fingerprint() {
            let options = []
            let cachedFingerprint = window.localStorage.getItem("_currentUser")

            if (cachedFingerprint !== null) {
                this._currentUser = cachedFingerprint
                return;
            }

            Fingerprint2.get(options, (components) => {
                var values = components.map((component) => component.value)
                var murmur = Fingerprint2.x64hash128(values.join(''), 31)
                this._currentUser = murmur;

                window.localStorage.setItem("_currentUser", murmur);
            })
        }

        if (window.requestIdleCallback) {
            requestIdleCallback(fingerprint.bind(this))
        } else {
            setTimeout(fingerprint.bind(this), 500)
        }
        /**
         * Load from local storage here
         * Fingerprint here and set it to the _currentUser
         * Check if already answered and set it correspondingly
         */
    }

