import React from 'react'
import { observer, inject } from 'mobx-react'
import '../../styles/Questionnaire.css'
import { QuestionItem } from '../items/QuestionItem'
import { useState, useEffect } from 'react'
import { Redirect } from 'react-router-dom'
import { CSSTransition } from 'react-transition-group';
import { Button, Progress, Skeleton } from 'antd'
import "../../styles/Questionnaire.css"

/**
 * TODO:
 * - React-Spring general animations
 */

const Questionnaire = inject("store")(observer((props) => {
    /**
     * Fetch questions here
     */
    const [ questions, setQuestions ] = useState([]);
    const [ loading, setLoading ] = useState(true);

    const [ currentQuestionId, setCurrentQuestionId ] = useState(0);
    const [ currentQuestion, setCurrentQuestion ] = useState({});

    const [ currentlySelectedAnswer, setCurrentlySelectedAnswer] = useState("");
    const [ buttonLoading, setButtonLoading ] = useState(false);
    const [ redirect, setRedirect ] = useState(false);

    useEffect(() => {
        async function fetchQuestions() {
            props.store.fetchQuestions().then((jsonArray) => {
                setQuestions(jsonArray);
                setCurrentQuestion(jsonArray[0]);
                setCurrentQuestionId(0);
                setLoading(false);
            })
        }

        fetchQuestions();
    }, []);

    const nextQuestion = (e) => {
        setButtonLoading(true);
        setTimeout(() => {
            if (currentlySelectedAnswer !== "") {
                let newQuestionId = currentQuestionId + 1;
                setCurrentQuestionId(newQuestionId);
                setCurrentQuestion(questions[newQuestionId]);
                setCurrentlySelectedAnswer("");
            }
            setButtonLoading(false);
        }, 800);
    }

    const sendResults = () => {
        setButtonLoading(true);
        setTimeout(() => {
            props.store.submitQuestionnaire(questions);
            setButtonLoading(false);
            setRedirect(true);
        }, 1000)
    }

    const handleSelectedAnswer = (event) => {
        let selectedAnswer = "" + event.currentTarget.value;
        setCurrentlySelectedAnswer(selectedAnswer);
        let tempQuestions = questions
        tempQuestions[currentQuestionId].selectedAnswer = selectedAnswer;
        setQuestions(tempQuestions)
    }
    
    if (redirect) {
        return(<Redirect to="/studies"/>)
    }

    if (!loading && questions.length > 0 && currentQuestion !== {}) {
        const possibleAnswers = currentQuestion.possibleAnswers.map((el) => ({ content: el, type: el, answer: el }))
        return (
            <div className="questionnaireContainer">
                <div className="questionnaireHeader">
                    <h1>
                        Let us get to know you. 
                    </h1>
                    <h3>
                        Answer a few questions about your interests :)
                    </h3>
                </div>
                <CSSTransition
                    className="questionnaire"
                    as="div" timeout={300} classNames="questionnaire">
                    <div>
                        <div className="progressContainer">
                            <Progress className="questionProgress" percent={Number((currentQuestionId + 1)*100/questions.length).toFixed(1)} status="active"/>
                        </div>
                        <QuestionItem content={currentQuestion.question} answerOptions={possibleAnswers} onAnswerSelected={handleSelectedAnswer}/>
                            <Button 
                                loading={buttonLoading}
                                disabled={currentlySelectedAnswer === ""}
                                className="actionButton"
                                onClick={currentQuestionId < questions.length - 1 ? nextQuestion : sendResults}>
                                { currentQuestionId < questions.length - 1 ? "Next" : "Finish" }
                        </Button>
                    </div>
                </CSSTransition>
            </div>
        )
    } else {
        return (<div className="questionnaireContainer">
                    <div className="questionnaireHeader">
            <h1>
                Let us get to know you. 
            </h1>
            <h3>
                Answer a few questions about your interests :)
            </h3>
            </div>
            <Skeleton className="questionnaire" paragraph={{ rows: 5 }} active/>
        </div>)
    }
}))


export default Questionnaire
