import React from 'react'
import { Checkbox } from 'antd'

function ChecklistItem(props) {
    console.log(props.resources)
    return (
        <div className="task-checkbox">
            <Checkbox onChange={props.onComplete} size="huge" disabled={props.disabled}>
                <h3>
                    {props.task}
                </h3>
            </Checkbox>
            {
                props.resources !== null && props.resources !== undefined && props.resources.length > 0 ? 
                <div className="additionalResources">
                    {props.resources.map((el, i) => {
                        return (
                        <div className="subResources" key={i}>
                            - <a href={el.url}>{el.explanation}</a>
                        </div>);
                    })}
                </div> : <></>
            }
        </div>
    )
}

export default ChecklistItem
