import React, { useEffect, useState } from 'react'
import { observer, inject } from 'mobx-react'
import '../../styles/StudyPage.css'
import { Icon, Divider, Progress } from 'semantic-ui-react';
import { Link, Redirect } from 'react-router-dom'
import { Skeleton, Statistic, Tooltip, Typography, notification } from 'antd';
import ChecklistItem from '../items/ChecklistItem';
import { PoundOutlined , UserOutlined, CheckOutlined } from '@ant-design/icons'; 
import Paragraph from 'antd/lib/typography/Paragraph';
import Title from 'antd/lib/typography/Title';


const StudyPage = inject("store")(observer((props) => {

    const [ loading, setLoading ] = useState(true);
    const [ progress, setProgress ] = useState(0);
    const [ redirect, setRedirect ] = useState(false);

    const completeItem = (e) => {
        if (e.target.checked) {
            setProgress(progress + 1)
        } else {
            setProgress(progress - 1)
        }
    }

    let pageId = parseInt(props.studyId)
    
    useEffect(() => {
        async function fetchStudyPage(id) {
            props.store.fetchStudyPage(id).then((jsonArray) => {
                /**
                 * I'll use the object directly from the store
                 */
                setLoading(false);
            })
        }

        fetchStudyPage(pageId);
    }, []);

    if (redirect) {
        return(<Redirect to="/studies"/>)
    }

    let study = props.store.currentlyViewingStudy;

    if (study.checklist !== undefined ){
        if((progress/study.checklist.length) === 1) {
            notification.open({
                message: 'Congratiulations on completing ' + study.name + "!",
                description:
                  'Hope you liked it! You can take a look at the rest of the suggestions by clicking on this notification.',
                onClick: () => {
                    setRedirect(true);
                },
                duration: 9,
                icon: <CheckOutlined/>
            });
        }
    }

    if (loading) {
        return (
            <div className="root-studies-container">
                <div className="studies-header">
                    <Skeleton active/>
                </div>
                <div className="studies-container"><Skeleton active/><Skeleton active/></div>
            </div>
        )
    }

    return (
        <div className="study-page-root">
            <div className="study-page-header">
                <Link to="/studies" className="back-button">
                    <Icon size={'huge'} name="arrow left"/>
                </Link>
                <h1>
                    {study.name}
                </h1>
                <h3>
                    {study.suitedFor}
                </h3>
            </div>
            <div className="study-page-container">
                <div className="study-page-image">
                    <img className="study-page-img" alt={study.name + " image"} src={study.image}/>
                </div>
                <Divider/>
                <center>
                    <p>Your Progress</p>
                </center>
                <Progress progress percent={progress*100/study.checklist.length} active indicating/>
                <div className="study-page-description">
                    <div className="study-page-textbox">
                    <Typography>
                        <Title level={3}>General Introduction</Title>
                        <Paragraph>
                            {study.description}
                        </Paragraph>
                    </Typography>
                    </div>
                    <Divider/>
                    <div className="study-page-value">
                        {study.checklist.map((item) => 
                            <ChecklistItem key={item.task} onComplete={completeItem} disabled={false} task={item.task} resources={item.resources} url={item.url}/>)
                        }
                        <Tooltip title="Coming soon!">
                            <div>
                                <ChecklistItem disabled={true} task="Complete short interactive course"/>
                            </div>
                        </Tooltip>
                    </div>
                </div>
                <Divider/>
                <div className="study-page-statistics">
                    <Statistic
                        className="statistic-item"
                        title="Median Annual Salary"
                        value={study.statistics.medianSalary}
                        precision={0}
                        valueStyle={{ color: '#3f8600' }}
                        prefix={<PoundOutlined />}
                        suffix=""
                    />
                    <Statistic
                        className="statistic-item"
                        title="Applications"
                        value={study.statistics.peoplePerYear}
                        precision={0}
                        valueStyle={{ color: '#3f3300' }}
                        prefix={<UserOutlined />}
                        suffix="people per year"
                    />
                </div>
                
            </div>
        </div>
    )
}))

export default StudyPage
